import {isString, isEmpty} from 'lodash';

import {SOURCE_VECTOR, SOURCE_WMS} from 'Map/tier/styleParts/styleResolver';
import initializeTierOptions from 'Map/configurer/initializeTierOptions';

function absoluteUrl(partialUrl) {
    if (!partialUrl.startsWith('http')) {
        const modifiedPartialUrl = partialUrl.startsWith('/') ? partialUrl : `/${partialUrl}`;
        return window.location.protocol + '//' + window.location.host + modifiedPartialUrl;
    }
    return partialUrl;
}

export default function initializeMapOptions(inputOptions) {
    const options = initializeTierOptions(inputOptions);

    if (!isString(options.basePath)) {
        options.basePath = '/sdk';
    }

    if (!isString(options.stylesDirectoryPath)) {
        options.stylesDirectoryPath = '/styles';
    }

    if (isEmpty(options.container)) {
        throw new Error('"container" option is required');
    }

    if (options.style && options.theme) {
        throw new Error('You should specify only "style" or "theme" option');
    }

    if (!options.theme && !options.style) {
        options.theme = {
            style: 'main',
            layer: 'basic',
            source: SOURCE_VECTOR
        };
    }

    if (options.theme && options.theme.source === SOURCE_WMS && !options.theme.format) {
        options.theme.format = 'image/png';
    }

    if (!isString(options.glyphs)) {
        options.glyphs = absoluteUrl(`${options.basePath}/glyphs/{fontstack}/{range}.pbf`);
    }

    if (!isString(options.sprite)) {
        options.sprite = absoluteUrl(`${options.basePath}/sprites/sprite`);
    }
    return options;
}



