
const HOSTS = ['a', 'b', 'c', 'd'];

export default function(type, layerId, urlPrototype, options) {
    const isWMS = type.startsWith('wms');
    const sourceId = 'raster-tiles-' + type;
    const style = {
        version: 8,
        sources: {
            [sourceId]: {
                type: 'raster',
                tileSize: isWMS ? 512 : 256,
                minzoom: 0,
                maxzoom: 22
            }
        },
        layers: [{
            id: layerId,
            type: 'raster',
            source: sourceId
        }]
    };
    const url =
        Object.keys(options).reduce((acc, optionName) => acc.replace(optionName, options[optionName]), urlPrototype);
    style.sources[sourceId].tiles = HOSTS.map(host => {
        const urlWithHostname = url.replace('{s}', host);
        const separator = urlWithHostname.includes('?') ? '&' : '?';
        return `https://${urlWithHostname}${separator}key={key}`;
    });
    return style;
}



