
export const SOURCE_RASTER = 'raster';
export const SOURCE_VECTOR = 'vector';
export const SOURCE_WMS = 'wms';

function getThemeBasedStyle(options, rasterStyleFactory, vectorStyleFactory, wmsStyleFactory) {
    switch (options.theme.source) {
    case SOURCE_RASTER:
        if (!rasterStyleFactory) {
            throw new Error('Raster source is not supported');
        }
        return rasterStyleFactory(options.theme, options.thickness);
    case SOURCE_VECTOR:
        if (!vectorStyleFactory) {
            throw new Error('Vector source is not supported');
        }
        return vectorStyleFactory(options.theme);
    case SOURCE_WMS:
        if (!wmsStyleFactory) {
            throw new Error('WMS source is not supported');
        }
        return wmsStyleFactory(options.theme);
    default:
        throw new Error('Unknown theme source: ' + options.theme.source);
    }
}

export default (options, rasterStyleFactory, vectorStyleFactory, wmsStyleFactory) => {
    if (options.style) {
        return options.style;
    } else if (options.theme) {
        return getThemeBasedStyle(options, rasterStyleFactory, vectorStyleFactory, wmsStyleFactory);
    }

    throw new Error('Neither "style" nor "theme" was provided in "options"');
};



