import {isString} from 'lodash';

import periodicUpdater from './incidentViewportPeriodicUpdater';
import styleResolver, {SOURCE_RASTER} from './styleParts/styleResolver';
import rasterIncidentTiles from './styleParts/rasterTrafficIncidentTiles';
import vectorTrafficIncidentTilesStyleResolver from './styleParts/vectorTrafficIncidentTilesResolver';
import sourceNameReplacer from './sourceNameReplacer';
import {trafficIncidentsUrlTransformer} from 'Map/tier/urlTransformers/urlTransformers';
import constants from '../../core/constants';
import updateLayersVisibility from 'Map/tier/styleUpdate/updateLayersVisibility';

export const ID = 'trafficIncidentsTiles';
export const VECTOR_STYLE_SOURCE_NAME = 'vectorTrafficIncidentTiles';

function replaceOrAddTrafficModelIdToUrl(url, trafficModelId) {
    const trafficModelIdParamPattern = /([?&])t=[^&]+/;
    if (trafficModelIdParamPattern.test(url)) {
        return url.replace(trafficModelIdParamPattern, `$1t=${trafficModelId}`);
    } else if (url.indexOf('?') === -1) {
        return url + `?t=${trafficModelId}`;
    } else {
        return url + `&t=${trafficModelId}`;
    }
}

function replaceTrafficModelIdInTilesStyle(style, trafficModelId) {
    Object.keys(style.sources).forEach((sourceName) => {
        const tiles = style.sources[sourceName].tiles;
        style.sources[sourceName].tiles = tiles.map((tile) => replaceOrAddTrafficModelIdToUrl(tile, trafficModelId));
    });
}

export class TrafficIncidentTilesTier {
    constructor(options, position) {
        this.position = position;
        this.style = null;
        this.options = options;
        this.trafficModelId = constants.VIEWPORT_DEFAULT_VALUE;
        this.urlTransformer = trafficIncidentsUrlTransformer(options.key, options.incidentTiles.theme.source,
            () => this.trafficModelId);
        this.visible = false;

        periodicUpdater.set(this.options.refresh, this.options.key);
    }
    getId = () => ID;
    getPosition = () => this.position;
    getStyle = () => this.style;
    setStyle = (style) => {
        sourceNameReplacer(style, VECTOR_STYLE_SOURCE_NAME);
        replaceTrafficModelIdInTilesStyle(style, this.trafficModelId);
        this.style = style;
    };
    getOptions = () => this.options;
    isVisible = () => this.visible;
    setVisible = (visible) => {
        this.visible = visible;
    };

    _getSourceName = () => {
        if (isString(this.style)) {
            throw new Error('Cannot find a source name for a not expanded style');
        }

        return Object.keys(this.style.sources)[0];
    };

    _refresh = async (map, trafficModelId) => {
        this._deregisterMapEventsHandlers();
        this.trafficModelId = trafficModelId;
        if (isString(this.style)) {
            this._registerMapEventsHandlers();
            return;
        }
        replaceTrafficModelIdInTilesStyle(this.style, trafficModelId);
        if (!this.isRaster()) {
            map.__om.style.sourceCaches[this._getSourceName()].clearTiles();
        }
        updateLayersVisibility(map, this.getStyle().layers, 'none');
        updateLayersVisibility(map, this.getStyle().layers, 'visible');
        this._registerMapEventsHandlers();
    };

    onBeforeAdd = (map) => {
        if (!this.options.basePath) {
            this.options.basePath = map._basePath;
        }
        if (!this.options.stylesDirectoryPath) {
            this.options.stylesDirectoryPath = map._stylesDirectoryPath;
        }
        const styleResolverOptions = {...this.options.incidentTiles,
                                      basePath: this.options.basePath,
                                      stylesDirectoryPath: this.options.stylesDirectoryPath};
        this.style = styleResolver(styleResolverOptions, theme => rasterIncidentTiles(theme.style), theme =>
            vectorTrafficIncidentTilesStyleResolver(styleResolverOptions, theme));
    };

    onAdd = (map) => {
        this.map = map;
        this._registerMapEventsHandlers();
        periodicUpdater.addOnModelIdRetrievedCallback(ID, async (trafficModelId) => {
            await this._refresh(map, trafficModelId);
        });
    };

    onRemove = () => {
        periodicUpdater.removeOnModelIdRetrievedCallback(ID);
        this._deregisterMapEventsHandlers();
    };

    getUrlTransformer = () => this.urlTransformer;

    isRaster = () => this.options.incidentTiles.theme.source === SOURCE_RASTER;

    _mapEventHandler = () => {
        periodicUpdater.refreshTrafficModelIdAndExecuteCallbacks();
    };

    _registerMapEventsHandlers = () => {
        this.map.on('dragend', this._mapEventHandler);
        this.map.on('zoomend', this._mapEventHandler);
        this.map.on('pitchend', this._mapEventHandler);
    };

    _deregisterMapEventsHandlers = () => {
        this.map.off('dragend', this._mapEventHandler);
        this.map.off('zoomend', this._mapEventHandler);
        this.map.off('pitchend', this._mapEventHandler);
    };
}

export default (options, position) => {
    return new TrafficIncidentTilesTier(options, position);
};



