import tierProxy from 'Map/tier/tierProxy';
import tierStyleRetriever from 'Map/map/tierStyleRetriever';
import setTierInStyleOnPosition from 'Map/tier/styleUpdate/addTier';
import tierInStylePositionDesignator from 'Map/tier/styleUpdate/tierInStylePositionDesignator';
import removeTierFromStyle from 'Map/tier/styleUpdate/removeTier';
import modifyTileCacheForCertainTiers from 'Map/map/tileCacheModifier';
import converters from 'Services/services/converters';
import {SYMBOLS_ID as SYMBOLS_TIER_ID, ID as BASE_TIER_ID} from 'Map/tier/mapTier';
import updateLayersVisibility from 'Map/tier/styleUpdate/updateLayersVisibility';

/**
 * @class Map
 * @namespace Maps
 */

class ExtendedMap {
    constructor(tiersContainer, {language, geopoliticalView, basePath, stylesDirectoryPath} = {}) {
        this._tiersContainer = tiersContainer;
        this._tierStyleRetriever = tierStyleRetriever();
        this._language = language;
        this._geopoliticalView = geopoliticalView;
        this._basePath = basePath;
        this._stylesDirectoryPath = stylesDirectoryPath;
    }

    _downloadTierStyleIfNeeded = async (tier) => {
        await this._tierStyleRetriever.fetch(tier);
    };

    _addTierToTheContainer = async (tier) => {
        this._tiersContainer.addTier(tier);
    };

    _addTierToMapStyle = (tier) => {
        const positionDesignator = tierInStylePositionDesignator(this, this._tiersContainer.getTiers());
        const beforeLayerId = positionDesignator.findBeforeLayerIdFor(tier);
        setTierInStyleOnPosition(this, tier, beforeLayerId);
    };

    hasTier = (tierId) => {
        return this._tiersContainer.hasTier(tierId);
    };

    _getBaseTier = () => {
        return this._getTier(BASE_TIER_ID);
    };

    _getSymbolsTier = () => {
        return this._getTier(SYMBOLS_TIER_ID);
    };

    _updateSymbolsTierIfNecessary = (tier, visibility) => {
        const baseTier = this._getBaseTier();
        if (!baseTier.isRaster()) {
            return;
        }

        const symbolsTier = this._getSymbolsTier();

        const position = tier.getPosition();
        const baseAndSymbolsLayerCount = 2;
        const existenceCondition = visibility ? this._tiersContainer.length() > baseAndSymbolsLayerCount :
            this._tiersContainer.length() <= baseAndSymbolsLayerCount;
        if (baseTier.getPosition() < position && position < symbolsTier.getPosition() && existenceCondition) {
            symbolsTier.setVisible(visibility);
            updateLayersVisibility(this, symbolsTier.getStyle().layers, visibility ? 'visible' : 'none');
        }
    };

    _enableSymbolsTierIfNecessary = (tier) => {
        this._updateSymbolsTierIfNecessary(tier, true);
    };

    /**
     * Adds a tier.
     *
     * @method addTier
     * @param {Object} tier For example, a TrafficIncidentTier or TrafficFlowTilesTier instance.
     * @return {Promise<boolean>} Returns "false" if a tier was already added, "true" otherwise.
     */
    addTier = async (tier) => {
        tier = tierProxy(tier);
        if (this.hasTier(tier.getId())) {
            return false;
        }
        await tier.onBeforeAdd(this);
        await this._addTierToTheContainer(tier);
        await this._downloadTierStyleIfNeeded(tier);
        this._addTierToMapStyle(tier);
        await tier.onAdd(this);
        this._enableSymbolsTierIfNecessary(tier);
        modifyTileCacheForCertainTiers(this);
        return true;
    };

    _removeTierFromMapStyle = (tier) => {
        const shouldTierSourceBeRemoved = true; //for now, but we need to do it more smartly later
        removeTierFromStyle(this, tier, shouldTierSourceBeRemoved);
    };

    _getTier = (tierId) => {
        return this._tiersContainer.getTier(tierId);
    };

    _removeTierFromContainer = async (tierId) => {
        const tier = this._tiersContainer.removeTier(tierId);
        await tier.onRemove(this);
    };

    _disableSymbolsTierIfNecessary = (tier) => {
        this._updateSymbolsTierIfNecessary(tier, false);
    };

    /**
     * Removes a tier.
     *
     * @method removeTier
     * @param {String} tierId The tier id.
     * @return {Promise<boolean>} Returns "false" if a tier was not in the list of tiers, "true" otherwise.
     */
    removeTier = async (tierId) => {
        if (!this.hasTier(tierId)) {
            return false;
        }
        const tier = this._getTier(tierId);
        await this._removeTierFromContainer(tierId);
        this._removeTierFromMapStyle(tier);
        this._disableSymbolsTierIfNecessary(tier);
        return true;
    };

    /**
     * This method sets a language for a given map. This setting will affect only map tiles and not other
     * services, e.g., search. Providing an empty string ('') as an input value will unset this option. Supported
     * languages can be obtained from the list of:
     * <a href="https://developer.tomtom.com/maps-api/maps-api-documentation-vector/tile#listOfSupportedLanguages">
     *     Supported Languages</a> page.
     *
     * @method setLanguage
     * @param {String} language
     * @return {Promise}
     */
    setLanguage = async (language) => {
        language = converters.language(language, 'maps');
        this._language = language;
        return Promise.all(this._tiersContainer.getTiers().map((tier) => tier.setLanguage(language)))
            .then((results) => {
                return results.some((result) => result);
            });
    };

    /**
     * This method returns a language set.
     *
     * @method getLanguage
     * @return {String}
     */
    getLanguage = () => {
        return this._language;
    };

    /**
     * This method sets a geopolitical view for a given map. This setting will only affect map tiles and not
     * other services, e.g., search. Providing an empty string ('') as an input value will unset this option. Supported
     * geopolitical views can be obtained from
     * <a href="https://developer.tomtom.com/maps-api/maps-api-documentation-vector/tile#request-data">
     *     here</a>.
     *
     * @method setGeopoliticalView
     * @param {String} geopoliticalView
     * @return {Promise}
     */
    setGeopoliticalView = (geopoliticalView) => {
        geopoliticalView = converters.geopoliticalView(geopoliticalView);
        this._geopoliticalView = geopoliticalView;
        return Promise.all(this._tiersContainer.getTiers().map((tier) => tier.setGeopoliticalView(geopoliticalView)))
            .then((results) => {
                return results.some((result) => result);
            });
    };

    /**
     * This method returns a geopolitical view set.
     *
     * @method getGeopoliticalView
     * @return {String}
     */
    getGeopoliticalView = () => {
        return this._geopoliticalView;
    };

    /**
     * Returns a TomTomAttributionControl instance.
     *
     * @method getAttributionControl
     * @return {Maps.TomTomAttributionControl}
     */
    getAttributionControl = () => {
        return this.__om._attributionControl || this._attributionControl;
    }
}

export default (tiersContainer, options) => {
    return new ExtendedMap(tiersContainer, options);
};



