export default (map, tier, beforeLayerId, visibility) => {
    tier.getStyle().layers.slice().reverse().forEach((layer) => {
        if (!map.getLayer(layer.id)) {
            if (beforeLayerId) {
                map.addLayer(layer, beforeLayerId);
            } else {
                map.addLayer(layer);
            }
        }
        map.setLayoutProperty(layer.id, 'visibility', visibility);
        beforeLayerId = layer.id;
    });
};



