import T from 't.js';

export default function renderCopyrights(json) {
    const copyright = new T('<div id=\'copyrightMessage\'>' +
        '<h4>General Copyrights:</h4>' +
        '{{@generalCopyrights}}<p>{{=_val}}</p>{{/@generalCopyrights}}' +
        '{{@regions}}<h4>{{=_val.country.label}}:</h4>' +
        '{{_val.copyrightImages}}<img style=\'width: 200px;float:left;\' ' +
        'src=\'{{=_val.copyrightImages}}\'/>{{/_val.copyrightImages}}' +
        '{{@_val.copyrights}}<p>{{=_val}}</p>{{/@_val.copyrights}}{{/@regions}}' +
        '</div>');
    return copyright.render(json);
}



