import {assign} from 'lodash';

import {rasterTrafficFlowLayer} from '../../endpoints';
import rasterStylesFactory from './rasterStylesFactory';

const LAYER_NAMES = {
    ABSOLUTE: 'tomtomRasterTrafficFlowAbsolute',
    RELATIVE: 'tomtomRasterTrafficFlowRelative',
    RELATIVE_DELAY: 'tomtomRasterTrafficFlowRelativeDelay',
    REDUCED_SENSITIVITY: 'tomtomRasterTrafficFlowReducedSensitivity'
};

function styleChooser(thickness) {
    const options = {
        '{thickness}': thickness
    };

    return {
        ABSOLUTE: rasterStylesFactory(
            'traffic-flow',
            LAYER_NAMES.ABSOLUTE,
            rasterTrafficFlowLayer,
            assign(options, {'{style}': 'absolute'})
        ),
        RELATIVE: rasterStylesFactory(
            'traffic-flow',
            LAYER_NAMES.RELATIVE,
            rasterTrafficFlowLayer,
            assign(options, {'{style}': 'relative'})
        ),
        RELATIVE_DELAY: rasterStylesFactory(
            'traffic-flow',
            LAYER_NAMES.RELATIVE_DELAY,
            rasterTrafficFlowLayer,
            assign(options, {'{style}': 'relative-delay'})
        ),
        REDUCED_SENSITIVIY: rasterStylesFactory(
            'traffic-flow',
            LAYER_NAMES.REDUCED_SENSITIVITY,
            rasterTrafficFlowLayer,
            assign(options, {'{style}': 'reduced-sensitivity'})
        )
    };
}

export default function(thickness, style) {
    return styleChooser(thickness)[style.toUpperCase().replace('-', '_')];
}



