import { isEqual, isString } from 'lodash/core';

/**
 * @ignore
 * Consider NaN and Infinite as not a number.
 * @param {*} val
 * @return {Boolean}
 */
function isValidNumber(val) {
    return typeof val === 'number' && isFinite(val);
}

/**
 * @ignore
 * @param {*} val
 * @return {Boolean}
 */
function isNonEmptyString(val) {
    return typeof val === 'string' && val.length > 0;
}

/**
 * @ignore
 * Used instead of Number.isNaN
 * @param {*} val
 * @return {Boolean}
 */
function numberIsNaN(val) {
    // only true for NaN
    return val !== val; // eslint-disable-line no-self-compare
}

/**
 * @ignore
 * Returns TRUE if the value is not undefined, null, or NaN.
 * @param {*} val
 * @return {Boolean}
 */
function isValidValue(val) {
    return val !== undefined && val !== null && !numberIsNaN(val);
}

/**
 * @ignore
 * Returns a function that checks if a value passed as an argument is an instance of a specific class.
 * @param {Object} classObject
 * @return {Function}
 */
function createInstanceChecker(classObject) {
    return function(val) {
        return val instanceof classObject;
    };
}

function cloneProperties(object, clonedObject) {
    for (const property in object) {
        if (object.hasOwnProperty(property)) {
            if (Array.isArray(object[property])) {
                clonedObject[property] = object[property].slice(0);
            } else if (typeof object[property] === 'object') {
                clonedObject[property] = {};
                cloneProperties(object[property], clonedObject[property]);
            } else {
                clonedObject[property] = object[property];
            }
        }
    }
}

function getValueOrEmptyObject(value) {
    return value === undefined ? {} : value;
}

function addNewField(result, field, newFields) {
    result[field] = getValueOrEmptyObject(result[field]);
    for (const property in newFields[field]) {
        if (newFields[field].hasOwnProperty(property)) {
            result[field][property] = newFields[field][property];
        }
    }
}

function extend(obj, src) {
    for (const key in src) {
        if (src.hasOwnProperty(key)) {
            obj[key] = src[key];
        }

    }
    return obj;
}

export default {
    isEqual: isEqual,
    isString: isString,
    isValidNumber: isValidNumber,
    isValidValue: isValidValue,
    isNonEmptyString: isNonEmptyString,
    createInstanceChecker: createInstanceChecker,

    addFields: function(newFields, defaultFields) {
        const result = defaultFields || this.defaultFields || {};
        for (const field in newFields) {
            if (newFields.hasOwnProperty(field)) {
                addNewField(result, field, newFields);
            }
        }
        return result;
    },

    clone: function(object) {
        const clonedObject = {};
        cloneProperties(object, clonedObject);
        return clonedObject;
    },

    encodeQuery: query => encodeURIComponent(query),

    encodeStringForRegex: value => value.replace(/[-[\]/{}()*+?.\\^$|]/g, '\\$&'),

    extend: extend,

    /**
     * @param {Object} sourceObj
     * @param {String} deprecatedPropertyName
     * @param {String} deprecatedPropertyReplacement
     */
    addDeprecatedProperty: function(sourceObj, deprecatedPropertyName, deprecatedPropertyReplacement) {
        Object.defineProperty(sourceObj, deprecatedPropertyName, {
            get: function() {
                console.warn('Usage of ' + deprecatedPropertyName +
                    ' property is deprecated. Please use ' + deprecatedPropertyReplacement + ' property.');
                return this[deprecatedPropertyReplacement];
            }
        });
    },

    pointRegex: /(-?\d+(?:\.\d+)?)(?:\s+|\s*,\s*)(-?\d+(?:\.\d+)?)/,

    circleRegex: /circle\((-?\d+(?:\.\d+)?)\s*,\s*(-?\d+(?:\.\d+)?)\s*,\s*(\d+)\)/
};



