import queryParamUpdater from 'Map/utils/queryParamUpdater';

const API_KEY_PARAM_NAME = 'key';

class BaseUrlTransformer {
    constructor(key, testMethodImpl, transformMethodImpl) {
        this._testMethodImpl = testMethodImpl;
        this._transformMethodImpl = transformMethodImpl;
        this._key = key;
    }

    test = (url) => {
        return this._testMethodImpl(url);
    };

    transform = (url) => {
        let modifiedUrl = queryParamUpdater(url, API_KEY_PARAM_NAME, this._key);

        if (this._transformMethodImpl) {
            modifiedUrl = this._transformMethodImpl(modifiedUrl);
        }

        return modifiedUrl;
    }
}

export default (key, testMethodImpl, transformMethodImpl) => {
    return new BaseUrlTransformer(key, testMethodImpl, transformMethodImpl);
};



