import {SOURCE_VECTOR} from 'Map/tier/styleParts/styleResolver';
import initializeTierOptions from 'Map/configurer/initializeTierOptions';

const validateIncidentDetailsOptions = (options) => {
    if (!options) {
        return undefined;
    }

    const incidentDetailsOptions = {...options};

    if (!incidentDetailsOptions.theme || !incidentDetailsOptions.theme.style) {
        incidentDetailsOptions.theme = {
            style: 's1'
        };
    }

    return incidentDetailsOptions;
};

const validateIncidentTilesOptions = (options) => {
    if (!options) {
        return undefined;
    }

    const incidentTilesOptions = {...options};

    if (!incidentTilesOptions.theme || !incidentTilesOptions.theme.style) {
        incidentTilesOptions.theme = {
            style: 's1',
            source: SOURCE_VECTOR
        };
    }

    return incidentTilesOptions;
};

export default function(inputOptions) {
    if (!inputOptions) {
        return null;
    }
    const options = initializeTierOptions(inputOptions);

    options.incidentDetails = validateIncidentDetailsOptions(options.incidentDetails);
    options.incidentTiles = validateIncidentTilesOptions(options.incidentTiles);

    options.refresh = !options.refresh || options.refresh < 30000 ? 30000 : options.refresh;

    return options;
}



