/**
 * @namespace Maps
 */

/**
  * @class Marker
  * @extends Maps.Evented
  * @description Creates a marker component
  * @example
  * ```javascript
  * var marker = new tt.Marker()
  * .setLngLat([30.5, 50.5])
  * .addTo(map);
  * ```
  * @constructor
  * @param {Object|HTMLElement} [options] Object with options or HTMLElement
  * @param {HTMLElement} [options.element] DOM element to use as a marker.
  * @param {String} [options.anchor = bottom] A string indicating the part of the Marker that should be positioned
  * closest to the coordinate set via Marker.setLngLat . Options are 'center', 'top', 'bottom', 'left',
  * 'right', 'top-left', 'top-right', 'bottom-left', and 'bottom-right' .
  * @param {Maps.PointLike} [options.offset] The offset in pixels as a PointLike object to apply relative to the
  * element's center. Negatives indicate left and up.
  * @param {String} [options.color = #3FB1CE] The color to use for the default marker if options.element is not
  * provided. The default is light blue.
  * @param {Boolean} [options.draggable] A boolean indicating whether or not a marker is able to be dragged to a
  * new position on the map.
  */

/**
  * Attaches the marker to a map
  * @method addTo
  * @type function
  * @param {Map} map Instance of a map
  * @returns {Maps.Marker} this
*/

/**
  * Removes the marker from a map
  * @method remove
  * @type function
  * @returns {Maps.Marker} this
*/

/**
  * Get the marker's geographical location.
  * The longitude of the result may differ by a multiple of 360 degrees from the longitude previously set by
  * setLngLat because Marker wraps the anchor longitude across copies of the world to keep the marker on screen.
  * @method getLngLat
  * @type function
  * @returns {Maps.LngLat}
*/

/**
  * Set the marker's geographical position and move it.
  * @method setLngLat
  * @type function
  * @param {Maps.LngLat} lnglat
  * @returns {Maps.Marker} this
*/

/**
  * Returns the Marker's HTML element.
  * @method getElement
  * @type function
  * @returns {HTMLElement} element
*/

/**
  * Binds a Popup to the Marker
  * @method setPopup
  * @type function
  * @param {Maps.Popup} popup an instance of the  Popup class. If undefined or null,
  * any popup set on this  Marker instance is unset
  * @returns {Maps.Marker} this
*/

/**
  * Returns the Popup instance that is bound to the Marker
  * @method getPopup
  * @type function
  * @returns {Maps.Popup} popup
*/

/**
  * Opens or closes the bound popup, depending on the current state
  * @method togglePopup
  * @type function
  * @returns {Maps.Marker} this
*/

/**
  * Get the marker's offset.
  * @method getOffset
  * @type function
  * @returns {Maps.Point}
*/

/**
  * Sets the offset of the marker
  * @method getOffset
  * @type function
  * @param {Maps.PontLike} offset The offset in pixels as a PointLike object to apply relative
  * to the element's center. Negatives indicate left and up.
  * @returns {Maps.Marker} this
*/

/**
  * Sets the draggable property and functionality of the marker
  * @method setDraggable
  * @type function
  * @param {Boolean} [shouldBeDraggable=false] Turns drag functionality on/off
  * @returns {Maps.Marker} this
*/

/**
  * Returns true if the marker can be dragged
  * @method isDraggable
  * @type function
  * @returns {Boolean}
*/

/**
  * Fired when dragging starts
  * @event dragstart
  * @type Maps.Marker
  */

/**
  * Fired while dragging
  * @event drag
  * @type Maps.Marker
  */

/**
  * Fired when the marker is finished being dragged
  * @event dragend
  * @type Maps.Marker
  */

import mapboxGl from 'mapbox-gl';
import markerSvg from 'Assets/svg/poi/marker.colors-black-white-green.svg';
import flagSvg from 'Assets/svg/poi/flag.colors-black-white.svg';

const outerElemDimensions = {
    width: 30,
    height: 36
};

const innerElemDimensions = {
    width: 30,
    height: 30
};

const markerSvgBlack = markerSvg.replace('<svg',
    `<svg width="${outerElemDimensions.width}" height="${outerElemDimensions.height}"`);

const flagSvgWhite = flagSvg
    .replace('<svg', `<svg width="${innerElemDimensions.width}" height="${innerElemDimensions.height}"`)
    .replace('<path', '<path fill="white"');

function renderMarkerElem() {
    const elem = document.createElement('div');
    elem.style.backgroundImage = 'none';
    elem.style.width = `${outerElemDimensions.width}px`;
    elem.style.height = `${outerElemDimensions.height}px`;
    elem.innerHTML = markerSvgBlack;

    const innerElem = document.createElement('div');
    innerElem.style.position = 'absolute';
    innerElem.style.top = 0;
    innerElem.style.left = 0;
    innerElem.style.width = `${innerElemDimensions.width}px`;
    innerElem.style.height = `${innerElemDimensions.height}px`;
    innerElem.style.backgroundImage = 'none';
    innerElem.innerHTML = flagSvgWhite;

    elem.appendChild(innerElem);

    return elem;
}

function getOptions(options) {
    if (options instanceof global.HTMLElement) {
        return options;
    }

    return {...options,
            element: options.element || renderMarkerElem(),
            anchor: options.anchor || 'bottom'
    };
}

export class Marker extends mapboxGl.Marker {
    constructor(options = {}) {
        super(getOptions(options));
    }
}



