import {isString, isObject, isInteger, inRange} from 'lodash';

import initializeTierOptions from 'Map/configurer/initializeTierOptions';

const TRAFFIC_FLOW_DEFAULT_THICKNESS = 4;

const validateThickness = (thickness) => {
    const returnValue = thickness || TRAFFIC_FLOW_DEFAULT_THICKNESS;

    if (!(isInteger(returnValue) && inRange(returnValue, 1, 21))) {
        throw new Error('Thickness must be an integer in range from 1 to 20.');
    }

    return returnValue;
};

export default function initializeTrafficFlowOptions(inputOptions) {
    if (!inputOptions) {
        return null;
    }

    const options = {
        ...initializeTierOptions(inputOptions),
        thickness: validateThickness(inputOptions.thickness)
    };

    if (inputOptions.theme && inputOptions.style) {
        throw new Error('You should specify only "style" or "theme" option for traffic flow');
    }

    if (!inputOptions.theme && !inputOptions.style) {
        options.theme = {
            style: 'absolute',
            layer: 'flow',
            source: 'vector'
        };
    } else {
        options.theme = {...inputOptions.theme};
        const isStylePlainObject = isObject(inputOptions.style) && !isString(inputOptions.style);
        options.style = isStylePlainObject ? {...inputOptions.style} : inputOptions.style;

        if (options.theme) {
            options.theme.layer = 'flow';
        }
    }

    if (!inputOptions.refresh) {
        options.refresh = 180000;
    } else {
        options.refresh = inputOptions.refresh;
        if (options.refresh < 30000) {
            options.refresh = 30000;
        }
    }

    return options;
}



