import json from './json';
import jsonp from './jsonp';

let useJsonp = false;
let useHttp = false;

const setUseJsonp = (use) => {
    useJsonp = use;
};

const setUseHttp = (use) => {
    useHttp = use;
};

const getProtocol = () => {
    return useHttp ? 'http' : 'https';
};

export default {
    useJsonp: setUseJsonp,
    useHttp: setUseHttp,
    protocol: getProtocol,

    get: (options) => {
        options.protocol = (options && options.protocol) || getProtocol();
        return useJsonp ? jsonp.get(options) : json.get(options);
    },

    post: (options) => {
        if (useJsonp) {
            console.warn('Jsonp is not supported, using json request.');
        }
        options.protocol = options.protocol || getProtocol();
        return json.post(options);
    }
};



