import periodicUpdater from './incidentViewportPeriodicUpdater';
import IncidentMarker from '../display/markers/IncidentMarker';
import constants from '../../core/constants';
import {incidentDetails} from 'Services/services/incidentDetails';
import { incidentDetailsResponseMerger } from './incidentDetailsResponseMerger';
import { isEmpty } from 'lodash';

export const ID = 'trafficIncidentsDetails';

function getZoom(map) {
    return Math.floor(map.getZoom());
}

/* eslint-disable */
/**
 * This is a tier for the Traffic Incident Details service.
 * @class TrafficIncidentDetailsTier
 * @private
 * @constructor
 * @param {Object} [options] Tier options.
 * @param {Boolean} [options.incidentDetails.diff] Flag indicating the use of the markers diff
 * algorithm to avoid removing all markers with each update.
 * @param {Object} [options.incidentDetails.markerOptions] Options used to
 * generate {{#crossLink "IncidentMarker"}}{{/crossLink}}.
 */
/* eslint-enable */
export class TrafficIncidentDetailsTier {
    constructor(options, position) {
        this.position = position;
        this.options = options;
        this.options.incidentDetails = options.incidentDetails || {};
        this.markerOptions = options.incidentDetails.markerOptions || {};
        this.updatePeriodicUpdater = true;
        this.trafficModelId = constants.VIEWPORT_DEFAULT_VALUE;
        this.incidentMarkers = Object.create(null);
        this.map = null;

        periodicUpdater.set(this.options.refresh, this.options.key);
    }

    getId = () => ID;
    getPosition = () => this.position;
    getStyle = () => this.style;
    getOptions = () => this.options;

    _propertiesIdSelector = (feature) => feature.properties.id;

    _clearIncidentMarkers = () => {
        if (!isEmpty(this.incidentMarkers)) {
            /* eslint-disable guard-for-in */
            for (const marker in this.incidentMarkers) {
                this.incidentMarkers[marker].remove();
            }
            /* eslint-enable guard-for-in */
            this.incidentMarkers = Object.create(null);
        }
    };

    _splitBoundingBox = (bBox) => {
        const minLon = bBox.getWest();
        const maxLon = bBox.getEast();
        const minLat = bBox.getSouth();
        const maxLat = bBox.getNorth();

        if (minLon < -180) {
            return [
                { minLon: -180, minLat: minLat, maxLon: maxLon, maxLat: maxLat },
                { minLon: 360 + minLon, minLat: minLat, maxLon: 180, maxLat: maxLat }
            ];
        }

        if (maxLon > 180) {
            return [
                { minLon: minLon, minLat: minLat, maxLon: 180, maxLat: maxLat },
                { minLon: -180, minLat: minLat, maxLon: maxLon - 360, maxLat: maxLat }
            ];
        }

        return [bBox];
    };

    _fetchIncidentDetails = () => {
        const requestOptions = {
            key: this.options.key,
            trafficModelID: this.trafficModelId,
            boundingBox: this.map.getBounds(),
            zoomLevel: getZoom(this.map),
            style: this.options.incidentDetails.theme.style
        };

        const boundingBoxes = this._splitBoundingBox(this.map.getBounds());
        const promises = [];
        boundingBoxes.forEach(boundingBox => {
            promises.push(incidentDetails({...requestOptions, boundingBox}).go());
        });

        return Promise.all(promises)
            .then(responses => {
                return incidentDetailsResponseMerger(responses);
            });
    };

    _updateIncidentMarkers = async () => {
        const trafficIncidents = await this._fetchIncidentDetails();
        const newFeaturesDictionary = this._mapResponseToFeaturesDictionary(trafficIncidents);

        this._updateExistingMarkers(this.incidentMarkers, newFeaturesDictionary);
        this._addNewMarkers(newFeaturesDictionary);
    };

    _updateExistingMarkers = (existingMarkers, newMarkers) => {
        Object.keys(existingMarkers).forEach((featureId) => {
            if (!newMarkers[featureId]) {
                existingMarkers[featureId].remove();
                delete existingMarkers[featureId];
            } else if (existingMarkers[featureId]) {
                existingMarkers[featureId].update(newMarkers[featureId]);
            } else {
                this._addMarker(newMarkers[featureId], featureId);
            }

            delete newMarkers[featureId];
        });
    }

    _addNewMarkers = (newMarkers) => {
        Object.keys(newMarkers).forEach((featureId) => {
            this._addMarker(newMarkers[featureId], featureId);
        });
    }

    _mapResponseToFeaturesDictionary = (response) => {
        return response.toGeoJson().features.reduce((result, feature) => {
            const current = {};
            current[this._propertiesIdSelector(feature)] = feature;
            return Object.assign(result, current);
        }, Object.create(null));
    }

    _addIncidentMarkers = async () => {
        const trafficIncidents = await this._fetchIncidentDetails();

        trafficIncidents.toGeoJson().features.forEach((feature) => {
            this._addMarker(feature);
        });
    }

    _addMarker = (feature, featureId = this._propertiesIdSelector(feature)) => {
        const marker = new IncidentMarker(this.markerOptions, feature);

        this.incidentMarkers[featureId] = marker;
        marker.addTo(this.map);
    }

    _refreshDetails = async (trafficModelId) => {
        this.trafficModelId = trafficModelId || this.trafficModelId;

        if (!this.trafficModelId || this.trafficModelId === constants.VIEWPORT_DEFAULT_VALUE) {
            return;
        }

        this.updatePeriodicUpdater = false;

        if (this.options.incidentDetails.diff) {
            await this._update();
        } else {
            await this._hide();
            await this._show();
        }

        this.updatePeriodicUpdater = true;
    };

    _mapEventHandler = () => {
        periodicUpdater.refreshTrafficModelIdAndExecuteCallbacks(ID);
    };

    _registerMapEventsHandlers = () => {
        this.map.on('dragend', this._mapEventHandler);
        this.map.on('zoomend', this._mapEventHandler);
        this.map.on('pitchend', this._mapEventHandler);
    };

    _deregisterMapEventsHandlers = () => {
        this.map.off('dragend', this._mapEventHandler);
        this.map.off('zoomend', this._mapEventHandler);
        this.map.off('pitchend', this._mapEventHandler);
    };

    _update = async () => {
        this._deregisterMapEventsHandlers();

        await this._updateIncidentMarkers();

        this._registerMapEventsHandlers();
    };

    onAdd = (map) => {
        this.map = map;
        this._registerMapEventsHandlers();

        periodicUpdater.addOnModelIdRetrievedCallback(ID, async (trafficModelId) => {
            await this._refreshDetails(trafficModelId);
        });
    };

    _show = async () => {
        if (this.updatePeriodicUpdater) {
            this._registerMapEventsHandlers();
        }

        if (this.options.incidentDetails.diff) {
            await this._updateIncidentMarkers();
        } else {
            await this._addIncidentMarkers();
        }
    };

    _hide = () => {
        if (this.updatePeriodicUpdater) {
            this._deregisterMapEventsHandlers();
        }

        this._clearIncidentMarkers();
    };

    onRemove = (map) => {
        if (!this.map) {
            this.map = map;
        }
        periodicUpdater.removeOnModelIdRetrievedCallback(ID);
        this._deregisterMapEventsHandlers();
        this._clearIncidentMarkers();
        delete this.map;
    }
}

export default (options, position) => {
    return new TrafficIncidentDetailsTier(options, position);
};



